.body {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.welcome-bar {
  display: flex;
  justify-content: left;
  align-items: left;
  margin-top: 3rem;
  height: 3rem;
  width: 73rem;
  border-radius: 3.125rem;
  box-sizing: border-box;
  background: #FF6B35;
}

.welcome-user {
  text-align: center;
}

.welcome-user p{
  padding-left: 5rem;
  color: #EFEFD0;
  font-weight: 600;
}

.hand-welcome {
  display: flex;
  align-items: center;
}

.hand {
  width: 8rem;
  height: 8rem;
  flex-shrink: 0;
  margin-top: -1rem;
}

.smack-logo {
  margin-left: -2rem;
  position: absolute;
}

.home-container{
  margin-top: 3rem;
  border-radius: 1.5625rem;
  background: #EFEFD0;
  width: 73.0625rem;
  height: 47rem;
  flex-shrink: 0; 
}

.song-container{  
  margin-top: -3rem;
  margin-left: -1rem;
  margin-right: 3rem;
}

.song-top{
  display: flex;
  justify-content: center;
  margin: 5rem;
  width: 11rem;
  height: 13rem;
  border-radius: 1.5625rem;
  background: #004E89;
  flex-shrink: 0;
}

.song-top p{
  color: #EFEFD0;
  text-align: center;
  font-size: 1rem;
  font-weight: 700;
  flex-direction: 0;
  margin-left: 2rem;
}

.song-top-x-container{
 padding-left: 2rem;
 margin-top: 1rem;
}

.movie-top{
  display: flex;
  justify-content: center;
  margin: 5rem;
  width: 11rem;
  height: 13rem;
  border-radius: 1.5625rem;
  background: #004E89;
  flex-shrink: 0;
}

.song-body{
  width: 17.875rem;
  height: 23.875rem;
  flex-shrink: 0;
  border-radius: 1.5625rem;
  background: #004E89;
  margin-left: 5rem;
  margin-top: -15rem;
  display: flex;
  justify-content: center;
}

.song-pic{
  width: 17rem;
  height: 15rem;
  background: white;
  margin-top: .5rem;
  border-radius: 0rem 1.5625rem 0rem 0rem;
}

.container-first-row{
  display: flex;
}

.movie-container{
  margin-top: -3rem;
}

.movie-top{
  display: flex;
  justify-content: left;
  width: 15rem;
  height: 13rem;
  border-radius: 1.5625rem;
  background: #004E89;
  flex-shrink: 0;
  margin-bottom: 0rem;
  margin-left: -.0001rem;
  
}

.movie-top p{
  margin-left: .5rem;
  color: #EFEFD0;
  text-align: center;
  font-size: 1.1875rem;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.movie-inside{
  margin-top: -10rem;
  width: 47.5rem;
  height: 22.8125rem;
  flex-shrink: 0;
  background:#004E89;
  display: flex;
  align-items: center;
  justify-content: center;

}

.movie-pic{
  
  width: 46rem;
  height: 20.5rem;
  flex-shrink: 0;
  border-radius: 0rem 1.5625rem 1.5625rem 1.5625rem;
  background: white;
}

.container-second-row{
  align-items: center;
  justify-content: center;
}

.fun-fact-container{
  width: 40.625rem;
  height: 14.75rem;
  flex-shrink: 0;
  border-radius: 1.5625rem;
  background: #004E89;
  margin-top: 3rem;
  margin-left: 4rem;
  align-items: center;
  justify-content: center;
}

.joke-container{
  margin: 3rem;
  align-items: center;
  justify-content: center;
}

.joke{
